import { Component, Input } from '@angular/core';
import { AccordionService } from './../../../services/accordion.service';
import { AccordionGroupComponent } from './accordion-group/accordion-group.component';

@Component({
  selector: 'uii-accordion',
  template: `<ng-content></ng-content>`,
  providers: [AccordionService]
})

export class AccordionComponent {
  /** turn on/off animation */
  @Input('isAnimated')
    set isAnimated(value: boolean) {
      this.accordionSvc.setIsAnimated(value);
    }
  /** if 'true', expanding one item will close others */
  @Input('closeOthers')
    set closeOthers(value: boolean) {
      this.accordionSvc.setCloseOthers(value);
    }

  protected groups: AccordionGroupComponent[] = [];

  constructor(private accordionSvc: AccordionService) {}

  closeOthersPanels(openGroup: AccordionGroupComponent) {
    if (!this.accordionSvc.getCloseOthers()) {
      return;
    }

    this.groups.forEach((group: AccordionGroupComponent) => {
      if (group !== openGroup) {
        group.isOpen = false;
      }
    });
  }

  addGroup(group: AccordionGroupComponent) {
    this.groups.push(group);
  }

}
