import { Injectable, ComponentFactoryResolver } from '@angular/core';
import { Subject } from 'rxjs';
import { Toast } from '../models/toast';
import { ToastComponent } from '../components/utilities/toast/toast.component';

@Injectable({
  providedIn: 'root'
})

export class ToastService {

    private toasts: Array<Toast> = [];
    private subject = new Subject<any>();
    private rootViewContainer: any;

    toastHovered = false;

    constructor(public factoryResolver: ComponentFactoryResolver) {}

    setToastRootViewContainerRef(viewContainerRef: any) {
        this.rootViewContainer = viewContainerRef;
    }

    addToastDynamicComponent() {
        const factory = this.factoryResolver.resolveComponentFactory(ToastComponent);
        const component = factory.create(this.rootViewContainer.parentInjector);
        this.rootViewContainer.insert(component.hostView);
    }

    error(description: string, title?: string, config?: {
        timeOut?: number,
        removeOnClick?: boolean
    }, callback?: () => void) {
        const message = new Toast('error',
                                title ? title : '',
                                description ? description : '',
                                config ? config.timeOut : 5000,
                                this.toasts.length,
                                config ? config.removeOnClick : false);
        this.pushToast(message);
        // an optional callback provided if only developers want to execute something after a toast has been pushed.
        if (callback) {
            callback();
        }
    }

    info(description: string, title?: string, config?: {
        timeOut?: number,
        removeOnClick?: boolean
    }, callback?: () => void) {
        const message = new Toast('info',
                                title ? title : '',
                                description ? description : '',
                                config ? config.timeOut : 5000,
                                this.toasts.length,
                                config ? config.removeOnClick : false);
        this.pushToast(message);
        // an optional callback provided if only developers want to execute something after a toast has been pushed.
        if (callback) {
            callback();
        }
    }

    success(description: string, title?: string, config?: {
        timeOut?: number,
        removeOnClick?: boolean
    }, callback?: () => void) {
        const message = new Toast('success',
                                title ? title : '',
                                description ? description : '',
                                config ? config.timeOut : 5000,
                                this.toasts.length,
                                config ? config.removeOnClick : false);
        this.pushToast(message);
        // an optional callback provided if only developers want to execute something after a toast has been pushed.
        if (callback) {
            callback();
        }
    }

    warning(description: string, title?: string, config?: {
        timeOut?: number,
        removeOnClick?: boolean
    }, callback?: () => void) {
        const message = new Toast('warning',
                                title ? title : '',
                                description ? description : '',
                                config ? config.timeOut : 5000,
                                this.toasts.length,
                                config ? config.removeOnClick : false);
        this.pushToast(message);
        // an optional callback provided if only developers want to execute something after a toast has been pushed.
        if (callback) {
            callback();
        }
    }

    private pushToast(message: any) {
        this.toasts.push(message);
        this.subject.next(this.toasts);
    }

    removeToast(index: number) {
        this.toasts = this.toasts.filter(toast => {
            return toast.index !== index;
        });
        this.subject.next(this.toasts);
    }

    retrieveToast() {
        return this.subject.asObservable();
    }
}
