import { Component, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'uii-datagrid',
  templateUrl: './data-grid.component.html',
  styleUrls: ['./data-grid.component.scss']
})
export class UiiDataGridComponent {
  @Input() gridTitle = '';
  @Input() columnTitle: Array<any> = [];
  @Input() rowTitle: Array<any> = [];
  @Input() data: Array<any> = [];
  @Input()
  set enableItemCLick(enable: boolean) {
    this._enableItemClick = enable;
  }

  @Output() itemClick = new EventEmitter();

  _enableItemClick = false;

  onItemClick(data: any) {
    if (this._enableItemClick) {
      this.itemClick.emit(data);
    }
  }

}
