export class Toast {
    type: string;
    title: string;
    description: string;
    timeOut: number;
    index: number;
    removeOnClick: boolean;

    constructor(type: string,
                title: string,
                description: string,
                timeOut: number,
                index: number,
                removeOnClick: boolean) {
        this.type = type;
        this.title = title;
        this.description = description;
        this.timeOut = timeOut;
        this.index = index;
        this.removeOnClick = removeOnClick;
    }
}
