import { Component, OnInit, ViewChild, ElementRef, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'uii-file-upload',
  templateUrl: './file-upload.component.html',
  styleUrls: ['./file-upload.component.scss']
})
export class FileUploadComponent implements OnInit {
  @ViewChild('file') fileTag: ElementRef;

  @Input() multiple = false;
  @Input() fileType = '.csv, .xlsx, application/pdf';
  @Input() maxFileSize = 1;
  @Input() maxFileType = 'MB';
  @Input() inputLabel = 'Upload';
  @Input() showPreview = true;
  @Input() showFileSize = true;
  @Input()
  set previewPosition(position: string) {
    // for single upload only
    this._previewPosition = (this.multiple) ? 'bottom' : position;
  }

  @Input()
  set fileList(files: any) {
    if (files) {
      this.onFileChange(files);
    }
  }

  @Output() filesSelected = new EventEmitter();

  files: Array<any> = [];
  sizes: Array<any> = [];
  fileSizeOver = false;
  _maxFileSize: number;
  _previewPosition = 'bottom';

  ngOnInit() {
    this.convertMaxFileSize();
  }

  private countFileSize(bytes: number) {
      if (bytes < 1024) {
        return bytes + 'B';
      } else if (bytes < 1048576) {
        return(bytes / 1024).toFixed(3) + ' KB';
      } else if (bytes < 1073741824) {
        return(bytes / 1048576).toFixed(3) + ' MB';
      } else {
        return(bytes / 1073741824).toFixed(3) + ' GB';
    }
  }

  onFileChange($event: any) {
    this.fileSizeOver = false;
    const files = $event.target.files;
    if (this.multiple) {
      this.attacheMultipleFiles(files);
    } else {
      this.attachSingleFile(files);
    }
  }

  onFileItemRemove(index: number) {
    this.files.splice(index, 1);
    this.sizes.splice(index, 1);
    if (this.files.length > 0) {
      this.fileSizeOver = false;
      this.files.forEach((item: any) => {
        if (item.size > this._maxFileSize) {
          this.fileSizeOver = true;
        }
      });

      if (this.fileSizeOver) {
        this.filesSelected.emit(null);
        this.fileTag.nativeElement.value = '';
        return;
      }

      this.filesSelected.emit(this.files);
    } else {
      this.filesSelected.emit(null);
    }
    this.fileTag.nativeElement.value = '';
  }

  private attacheMultipleFiles(files: any) {
    Array.from(files).forEach((item: any) => {
      if (item.size > this._maxFileSize) {
        this.fileSizeOver = true;
      }
      if (this.isFileExist(item.name) < 0) {
        this.files.push(item);
        this.sizes.push(this.countFileSize(item.size));
      }
    });
    this.fileTag.nativeElement.value = '';
    if (this.fileSizeOver) {
      this.filesSelected.emit(null);
      return;
    }

    this.filesSelected.emit(this.files);
  }

  private attachSingleFile(files: any) {
    this.files[0] = files[0];
    this.sizes[0] = this.countFileSize(files[0].size);
    if (files[0].size > this._maxFileSize) {
      this.fileSizeOver = true;
      this.filesSelected.emit(null);
      return;
    }

    this.filesSelected.emit(this.files);
  }

  convertMaxFileSize() {
    switch (this.maxFileType.toUpperCase()) {
      case 'KB':
        this._maxFileSize = this.maxFileSize * 1024;
        break;
      case 'MB':
        this._maxFileSize = this.maxFileSize * 1024 *  1024;
        break;
      case 'GB':
        this._maxFileSize = this.maxFileSize * 1024 * 1024 * 1024;
        break;
      case 'B':
        this._maxFileSize = this.maxFileSize;
        break;
      default:
        this._maxFileSize = this.maxFileSize;
        break;
    }
  }

  private isFileExist(fileName: string) {
    return this.files.findIndex(item => item.name === fileName);
  }

}
